<template>
  <nav class="fixed top-0 z-10 mb-2 h-[52px] w-full bg-white p-[14px] shadow-lg">
    <font-awesome-icon icon="fa-solid fa-angle-left" class="absolute left-4 mr-4 cursor-pointer text-2xl" @click="doRoute"/>
    <h3 class="gont text-2xl leading-6">{{title}}</h3>
  </nav>
</template>

<script>
export default {
  name: 'back-nav',
  description: '返回上一頁',
  props: {
    title: {
      type: [String],
    },
    routeTarget: { // TODO: 這邊看之後要不要寫成 Class
      type: [String, Object],
      default: null,
    },
  },
  methods: {
    doRoute() {
      if (this.routeTarget) {
        this.$router.push(this.routeTarget);
      } else {
        this.$router.push({ name: 'Home' });
      }
    },
  },
};
</script>

