// 取得設計師可預約的項目類別
const api = (request) => () => {
  const query = `
  query {
    getBookingItemCategory{
      id
      sort
      name
      bookingItem{
        id
        sort
        name
        lowestPrice
        highestPrice
      }
    }
  }
  `;
  return request({
    method: 'post',
    data: { query },
  });
};
export default api;
