// 指定預約
const api = (request) => ({ ...updateBooking }) => {
  const variables = JSON.stringify({ input: { ...updateBooking } });
  const query = `
  mutation updateBooking($input: BookingUpdateInput! ){
      updateBooking(input: $input){
      id
    }
  }
  `;
  return request({
    method: 'post',
    data: { query, variables },
  });
};
export default api;
